import "./index.scss";

import React, { useState } from "react";
import { connect } from "react-redux";
import uid from "uid";
import * as actions from "../../../../../redux/actions";

function CustomSelect(props: any) {
  const { selectedCategories } = props;
  console.log(selectedCategories);

  const [category, setCategory] = useState(
    selectedCategories.length > 0 ? selectedCategories[0] : "All categories"
  );
  const [isOpen, toggleCategory] = useState(false);

  const onSecondCityChange = (value: any) => {
    setCategory(value);
    toggleCategory(false);
  };

  const options = selectedCategories
    ? selectedCategories
        .filter((item: string) => item !== category)
        .map((item: string) => (
          <li
            key={uid()}
            className={`custom-select__item`}
            onClick={() => onSecondCityChange(item)}
          >
            {item}
          </li>
        ))
    : [];

  return (
    <div className={`custom-select`}>
      <div
        className={`custom-select__chosen ${
          isOpen && "custom-select__chosen--open"
        }`}
        onClick={() => toggleCategory(!isOpen)}
      >
        <span className={`custom-select__chosen-text`}>{category}</span>
      </div>
      {isOpen && <ul className={`custom-select__list`}>{options}</ul>}
    </div>
  );
}

export default connect((state) => state, actions)(CustomSelect);
