import React from "react";
import { IoIosArrowRoundForward } from "react-icons/io";

function Item(props: any) {
  console.log(props);
  const { title, images, category, price } = props.item;

  return (
    <article className={`scroll-slider__item`}>
      <div className="scroll-slider__top">
        <h3 className="scroll-slider__item-title">{category}</h3>
        <a href="#" className="scroll-slider__link">
          View all details <IoIosArrowRoundForward />
        </a>
      </div>
      <div className="scroll-slider__bottom">
        <p className={`scroll-slider__name`}>{title}</p>
        <p className={`scroll-slider__pricing`}>
          <strong className="scroll-slider__price">${price}</strong>
        </p>
      </div>
      <img src={images[0]} alt="dsaf" className={`scroll-slider__image`} />
    </article>
  );
}

export default Item;
