import { GET_CATEGORIES, GET_PRODUCTS } from "../constants";

const initialCategories = {
  categories: [],
  selectedCategories: [],
  products: []
};

type initialCategoriesType = typeof initialCategories;


export default (state: initialCategoriesType = initialCategories, action: { type: any; categories: any; selectedCategories: any, products: any  }) => {

  switch (action.type) {
    case GET_CATEGORIES:
      return {
        ...state,
        categories: action.categories,
        selectedCategories: action.selectedCategories,
      };
    case GET_PRODUCTS:
      return {
        ...state,
        products: action.products,
      };
    default:
      return state;
  }
};

