import { GET_CATEGORIES, GET_PRODUCTS } from "../constants";


export const getProducts = (data: any) => {
  console.log(data);
  
  return {
    type: GET_PRODUCTS,
    products: data,
  };
};

export const addCategories = (data: any, selectedCategories: any) => {
  
  return {
    type: GET_CATEGORIES,
    categories: data,
    selectedCategories
  };
};

