import "./index.scss";

import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import uid from "uid";
import axios from "axios";

import { MdSubject } from "react-icons/md";
import { IoIosLaptop, IoMdHeadset, IoIosClose } from "react-icons/io";
import { MdPhoneIphone } from "react-icons/md";
import * as actions from "../../../../redux/actions";

function Categories(props: any) {
  const { categories, addCategories, getProducts, products } = props;

  useEffect(() => {
    if (categories.length === 0) {
      getCategoriesData();
    }
    getProductsData();
  }, []);
  console.log(products);

  const createSelectedCategories = (categories: { routes: string[] }[]) => {
    let newSelectedCategories: any[] = ["All categories"];
    categories.map((item: { routes: string[] }) => {
      newSelectedCategories = [...newSelectedCategories, ...item.routes];
    });
    console.log(newSelectedCategories);
    return newSelectedCategories;
  };

  const getCategoriesData = async () => {
    try {
      const response = await fetch(
        `https://raw.githubusercontent.com/olegobiukh/other/master/api/ts-marketplace/categories.json`
      );
      const data = await response.json();
      return addCategories(data, createSelectedCategories(data));
    } catch (e) {}
  };

  const getProductsData = async () => {
    try {
      const response = await fetch(
        `https://raw.githubusercontent.com/olegobiukh/other/master/api/ts-marketplace/items.json`
      );
      const products = await response.json();
      return getProducts(products);
    } catch (e) {}
  };

  const [title, setTitle] = useState(
    categories.length > 0 ? categories[0].title : ""
  );
  const [isCategoriesOpen, toggleCategories] = useState(false);
  const [additionalTitle, setAdditionalTitle] = useState(
    categories.length > 0 ? categories[0].title : ""
  );
  const [additionalImage, setAdditionalImage] = useState(
    categories.length > 0 ? categories[0].image : ""
  );
  const [newLinks, setNewLinks] = useState(
    categories.length > 0 ? categories[0].routes : []
  );
  const [mainLink, setMainLink] = useState(
    categories.length > 0 ? categories[0].mainLink : ""
  );

  const hoverItem = (index: number) => {
    const newLinks = categories[index].routes;
    const newTitle: string = categories[index].title;
    const newMainLink: string = categories[index].mainLink;
    const newAdditionalTitle: any = categories[index].hasOwnProperty(
      "additionalTitle"
    )
      ? categories[index].additionalTitle
      : categories[index].title;
    const newAdditionalImage: any = categories[index].hasOwnProperty("image")
      ? categories[index].image
      : null;

    setNewLinks(newLinks);
    setMainLink(newMainLink);

    setTitle(newTitle);
    setAdditionalTitle(newAdditionalTitle);
    setAdditionalImage(newAdditionalImage);
  };

  console.log(JSON.stringify(newLinks));
  const formedSections = () => {
    return newLinks.map((item: React.ReactNode, index: any) => (
      <Link
        key={uid()}
        to={`/${item}`}
        className={`categories-addition__item`}
        onClick={() => toggleCategories(false)}
      >
        {item}
      </Link>
    ));
  };

  return (
    <div className={`categories`}>
      <div className={`categories__title`}>
        Categories
        {isCategoriesOpen ? (
          <IoIosClose
            onClick={() => toggleCategories(false)}
            size={23}
            className={`categories__toggler`}
          />
        ) : (
          <MdSubject
            onClick={() => toggleCategories(true)}
            className={`categories__toggler`}
          />
        )}
      </div>
      {isCategoriesOpen && (
        <div className={`categories__container`}>
          <ul className={`categories__list`}>
            {categories.length > 0 &&
              categories.map(
                (
                  item: {
                    mainLink: any;
                    icon: React.ReactNode;
                    title: React.ReactNode;
                  },
                  index: number
                ) => (
                  <Link
                    to={`/${item.mainLink}`}
                    className={`categories__item`}
                    onMouseOver={() => hoverItem(index)}
                    onClick={() => toggleCategories(false)}
                  >
                    {item.icon}
                    <span className={`categories__name`}>{item.title}</span>
                  </Link>
                )
              )}
          </ul>
          {newLinks.length > 0 && (
            <div className="categories-addition">
              <div className={`categories-addition__top`}>
                <h3 className="categories-addition__title">{title}</h3>
                <div className={`categories-addition__container`}>
                  <ul className={`categories-addition__list`}>
                    {formedSections()}
                  </ul>
                </div>
              </div>
              <Link to={`/${mainLink}`} onClick={() => toggleCategories(false)}>
                <div
                  className="categories-addition__banner"
                  style={{
                    backgroundImage: `url(${additionalImage})`,
                  }}
                >
                  <p className="categories-addition__banner-text">
                    Looking for more?
                  </p>
                  <h3 className="categories-addition__banner-title">
                    Get $100 Voucher* <br /> for
                    <span
                      className={`categories-addition__banner-title--normal`}
                    >
                      {additionalTitle}
                    </span>
                  </h3>
                </div>
              </Link>
            </div>
          )}
        </div>
      )}
    </div>
  );
}

export default connect((state) => state, actions)(Categories);
