import "./index.scss";
// import deals from "../../data/home-weekly.json";

import React from "react";
import { connect } from "react-redux";
import Item from "./Item";
import uid from "uid";
import * as actions from "../../redux/actions";

function ScrollSlider(props: any) {
  const { products } = props;
  // console.log(products);

  const getTenRundomNums = () => {
    let newRandom: string | any[] = [];
    while (newRandom.length <= 10) {
      let randomnumber =
        Math.floor(Math.random() * (products.length - 0 + 1)) + 1;
      newRandom = [...newRandom, products[randomnumber]];
    }
    return newRandom;
  };

  const deals = products ? getTenRundomNums() : [];

  const newItems = products ? deals.map((item: any) => <Item key={uid()} item={item} />) : null;

  return (
    <section className={`scroll-slider`}>
      <div className={`scroll-slider__wrapper`}>
        <h2 className={`scroll-slider__title`}>Show weekly deels</h2>
        <div className={`scroll-slider__container`}>{newItems}</div>
      </div>
    </section>
  );
}

export default ScrollSlider;
// export default connect((state) => state, actions)(ScrollSlider);
